.pagination {
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin: 20px 0;
  flex-wrap: wrap;
}

.pageItem {
  margin: 0 5px;
}

.pageLink {
  display: inline-block;
  width: 30px;
  height: 30px;
  border: none;
  border-radius: 50%;
  color: white;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}

.pageLink:hover,
.pageLink:focus,
.currentPage {
  color: white;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  border: none;
  width: 30px;
  height: 30px;
  background-color: orange;
  border-radius: 50%;
}

.prevButton,
.nextButton {
  display: inline-block;
  width: 30px;
  height: 30px;
  border: none;
  border-radius: 50%;
  font-size: 20px;
  padding: 0;
  color: white; 
  background-color: orange; 
  cursor: pointer;
  transition: background-color 0.3s;
  margin-left: 10px;
  margin-right: 10px;
}

.prevButton:disabled,
.nextButton:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.iconWrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
